import FieldMixin from './FieldMixin';

export default {
	mixins: [FieldMixin],

	props: {
		value: String | Array,
		multiple: {
			type: Boolean,
			default: false
		}
	},

	data()
	{
		return {
			valueM: this.value
		}
	},
	
	computed: {
		isRadio()
		{
			return !this.multiple && this.prop.SETTINGS.MULTIELEMENT == 'Y';
		}
	}
}
